"use client"
import React, {useState} from 'react'
import PersonalProfile from "../../../components/settings/personalProfile/personalProfile";
import Notification from "../../../components/settings/notification/notification";
import Security from "../../../components/settings/security/Security";

function page() {
  const [isPersonalProfile, setIsPersonalProfile] = useState(true);
  const [isNotification, setIsNotification] = useState(false);
  const [isSecurity, setIsSecurity] = useState(false);
  return (
    <div className="w-full px-4 py-2">
        <div className="flex space-x-6 text-sm text-[#5D56BD]">
          <div className= {`${isPersonalProfile ? 'border-b bottom-12-[#5D56BD]' : ''}`} onClick={()=>{
            setIsNotification(false)
            setIsSecurity(false)
            setIsPersonalProfile(true)
          }}>Personal Profile</div>

            <div  onClick={()=>{
            setIsNotification(true)
            setIsSecurity(false)
            setIsPersonalProfile(false)
          }}>Notification</div>

            <div  onClick={()=>{
            setIsNotification(false)
            setIsSecurity(true)
            setIsPersonalProfile(false)
          }}>Security</div>
          
       
        </div>
        {isPersonalProfile && <PersonalProfile/>}
            {isNotification && <Notification/>}
            {isSecurity && <Security/>}
      </div>
          
     
  )
}

export default page